/// <reference lib="webworker" />

import { Pit } from "../../pit/model/pit";
import { Race } from "../../race/model/race";
import { RaceConfig } from "../../race/model/race-config";
import { addMinutes, differenceInMinutes } from "date-fns";

addEventListener('message', ({ data }) => {
  const lastInterphoneChangePit = data.lastInterphoneChangePit as Pit | undefined;
  const race = data.race as Race | undefined;
  const raceConfig = data.raceConfig as RaceConfig;

  const lastChangeDate = getInterphoneChangeDate(lastInterphoneChangePit, race);
  const emptyBatteryDate = calculateEmptyBatteryDate(lastChangeDate, raceConfig);
  const remainingBatteryPercentage = calculateRemainingBatteryPercentage(emptyBatteryDate,raceConfig);

  postMessage({
    lastChangeDate: lastChangeDate,
    emptyBatteryDate: emptyBatteryDate,
    remainingBatteryPercentage: remainingBatteryPercentage,
  });
});

function getInterphoneChangeDate(lastInterphoneChangePit: Pit | undefined, race: Race | undefined): Date | undefined {
  if (lastInterphoneChangePit?.exitAt) {
    return lastInterphoneChangePit.exitAt;
  }
  if (race) {
    return race.startedAt;
  }
  return undefined;
}

function calculateEmptyBatteryDate(lastInterphoneChangeDate: Date | undefined, raceConfig: RaceConfig): Date | undefined {
  if (lastInterphoneChangeDate) {
    return addMinutes(lastInterphoneChangeDate, raceConfig.interphoneBatteryDurationMinute)
  }
  return undefined;
}

function calculateRemainingBatteryPercentage(emptyBatteryDate: Date | undefined, raceConfig: RaceConfig): number {
  if (emptyBatteryDate && raceConfig) {
    const remainingMinutes = differenceInMinutes(emptyBatteryDate, new Date());
    const percentage = (remainingMinutes / raceConfig.interphoneBatteryDurationMinute) * 100;
    const clampedPercentage = Math.max(0, Math.min(percentage, 100));
    return parseFloat(clampedPercentage.toFixed(0));
  }
  return 100;
}
